<template>
  <img :src="url" :alt="product.title" />
  <p class="text-white flex-1">{{ product.title }}</p>
  <div class="flex">
    <button class="btn" @click="cartStore.decQuantity(product)">-</button>
    <p class="py-1 px-4 mx-1 rounded-sm text-lg bg-gray-200">
      {{ product.quantity }}
    </p>
    <button class="btn" @click="cartStore.incQuantity(product)">+</button>
  </div>
  <p class="text-white">{{ product.price * product.quantity }} coins</p>
  <!-- delete -->
  <button class="ml-auto" @click="cartStore.deleteFromCart(product)">
    <span class="material-icons-outlined text-white">delete</span>
  </button>
</template>

<script setup>
import { useCartStore } from "@/stores/cartStore";
const cartStore = useCartStore();
const { product } = defineProps(["product"]);
const { filePath, url, uploadImage, deleteImage } = useStorage();
await uploadImage(product.img);
</script>

